<template>
  <!-- 确保模态框具有 fade 类 -->
  <div
    class="modal fade modal-xl"
    ref="modal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span>新增產品</span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-3">
                <label for="image" class="form-label">輸入圖片網址</label>
                <input
                  type="text"
                  class="form-control"
                  id="image"
                  placeholder="請輸入圖片連結"
                  v-model="tempProduct.imageUrl"
                />
              </div>
              <div class="mb-3">
                <label for="customFile" class="form-label">
                  或 上傳圖片
                  <i class="fas fa-spinner fa-spin"></i>
                </label>
                <input
                  type="file"
                  id="customFile"
                  class="form-control"
                  ref="fileInput"
                  @change="uploadImage"
                />
              </div>
              <img class="img-fluid" :src="tempProduct.imageUrl" alt="" />
              <!-- 延伸技巧，多圖 -->
              <div class="mt-5">
                <div class="mb-3 input-group">
                  <input type="url" class="form-control" placeholder="請輸入連結" />
                  <button type="button" class="btn btn-outline-danger">移除</button>
                </div>
                <div>
                  <button class="btn btn-outline-primary btn-sm d-block w-100">
                    新增圖片
                  </button>
                </div>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="mb-3">
                <label for="title" class="form-label">標題</label>
                <input
                  type="text"
                  class="form-control"
                  id="title"
                  placeholder="請輸入標題"
                  v-model="tempProduct.title"
                />
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="category" class="form-label">分類</label>
                  <input
                    type="text"
                    class="form-control"
                    id="category"
                    placeholder="請輸入分類"
                    v-model="tempProduct.category"
                  />
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label">單位</label>
                  <input
                    type="text"
                    class="form-control"
                    id="unit"
                    placeholder="請輸入單位"
                    v-model="tempProduct.unit"
                  />
                </div>
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="origin_price" class="form-label">原價</label>
                  <input
                    type="number"
                    class="form-control"
                    id="origin_price"
                    placeholder="請輸入原價"
                    v-model="tempProduct.origin_price"
                  />
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label">售價</label>
                  <input
                    type="number"
                    class="form-control"
                    id="price"
                    placeholder="請輸入售價"
                    v-model="tempProduct.price"
                  />
                </div>
              </div>
              <hr />

              <div class="mb-3">
                <label for="description" class="form-label">產品描述</label>
                <textarea
                  class="form-control"
                  id="description"
                  placeholder="請輸入產品描述"
                  v-model="tempProduct.description"
                ></textarea>
              </div>
              <div class="mb-3">
                <label for="content" class="form-label">說明內容</label>
                <textarea
                  class="form-control"
                  id="content"
                  placeholder="請輸入產品說明內容"
                  v-model="tempProduct.content"
                ></textarea>
              </div>
              <div class="mb-3">
                <div class="form-check">
                  <input
                    class="form-check-input"
                    type="checkbox"
                    :true-value="1"
                    :false-value="0"
                    id="is_enabled"
                    v-model="tempProduct.is_enabled"
                  />
                  <label class="form-check-label" for="is_enabled"> 是否啟用 </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-outline-secondary"
            data-bs-dismiss="modal"
            @click="cancelAddProduct"
          >
            取消
          </button>
          <button type="button" class="btn btn-primary" @click="addProduct">確認</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from "bootstrap";
import modalMixin from "../mixins/modalmixin";
import ToastList from "@/components/ToastList.vue";
import { getCurrentInstance } from "vue";

export default {
  mixins: [modalMixin],
  components: {
    ToastList,
  },
  data() {
    return {
      modal: null,
      tempProduct: {},
    };
  },
  methods: {
    addProduct() {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/admin/product`;
      const productInfo = {
        title: this.tempProduct.title,
        category: this.tempProduct.category,
        origin_price: this.tempProduct.origin_price,
        price: this.tempProduct.price,
        unit: this.tempProduct.unit,
        description: this.tempProduct.description,
        content: this.tempProduct.content,
        is_enabled: this.tempProduct.is_enabled,
        imageUrl: this.tempProduct.imageUrl,
      };
      this.$http.post(api, { data: productInfo }).then((res) => {
        console.log(res);
        if (res.data.success) {
          this.modal.hide();
          this.tempProduct = {};
          this.$emit("refresh-products");
          // 使用全局 emitter 实例
          this.$root.emitter.emit("push-message", {
            style: "success",
            message: "新增成功",
          });
        } else {
          this.$root.emitter.emit("push-message", {
            style: "danger",
            message: "新增失敗",
            content: res.data.message.join("`"),
          });
        }
      });
    },
    cancelAddProduct() {
      this.modal.hide();
      this.tempProduct = {};
    },
    uploadImage() {
      const uploadFile = this.$refs.fileInput.files[0];
      const formData = new FormData();
      formData.append("file-upload", uploadFile);
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/admin/upload`;
      this.$http.post(api, formData).then((res) => {
        console.log(res);
        if (res.data.success) {
          this.tempProduct.imageUrl = res.data.imageUrl;
        }
      });
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.modal = new Modal(this.$refs.modal);
    });
  },
};
</script>
