<template>
  <div class="modal" ref="modal" tabindex="-1">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header bg-danger">
          <h5 class="modal-title text-white">確定刪除嗎</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <p>
            確定刪除<span class="text-danger">{{ itemName }}</span
            >這項產品嗎(刪除後不可復原)
          </p>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">
            取消
          </button>
          <button type="button" class="btn btn-danger" @click="deleteProduct">確定</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from "bootstrap";
import modalMixin from "../mixins/modalmixin";

export default {
  data() {
    return {
      modal: null,
    };
  },
  props: {
    itemName: {
      type: String,
      required: true,
    },
    itemId: {
      type: Number,
      required: true,
    },
  },
  mixins: [modalMixin],
  methods: {
    deleteProduct() {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/admin/product/${this.itemId}`;
      this.$http.delete(api).then((res) => {
        console.log(res);
        if (res.data.success) {
          this.modal.hide();
          this.$emit('refresh-products')
        }
      });
    },
  },
  watch: {
    itemName(newValue) {
      console.log("itemName changed:", newValue);
    },
    itemId(newValue) {
      console.log("itemId changed:", newValue);
    },
  },
  mounted() {
    this.$nextTick(() => {
      console.log("itemName:", this.itemName);
      console.log("itemId:", this.itemId);
      this.modal = new Modal(this.$refs.modal);
    });
  },
};
</script>
