<template>
  <Loading :active="isLoading"></Loading>
  <ToastList></ToastList>
  asdaa
  <div class="addProductsBtnGroup mt-3" style="width: 100%; text-align: right">
    <button class="ml-auto btn btn-outline-primary" @click="openModal">新增產品</button>
  </div>
  <table class="table mt-3">
    <thead>
      <tr>
        <th scope="col">圖片</th>
        <th scope="col">分類</th>
        <th scope="col">產品名稱</th>
        <th scope="col">原價</th>
        <th scope="col">售價</th>
        <th scope="col">是否上架</th>
        <th scope="col">編輯</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(item, key) in products" :key="products.id">
        <td>
          <img
            class="mx-auto"
            :src="item.imageUrl"
            style="width: 80%; height: 100px"
            alt=""
          />
        </td>
        <td>{{ item.category }}</td>
        <td>{{ item.title }}</td>
        <td>{{ item.origin_price }}</td>
        <td>{{ item.price }}</td>
        <td>{{ item.is_enabled ? "已上架" : "未上架" }}</td>
        <td>
          <div class="buttonGroup d-flex gap-2">
            <button class="btn btn-outline-danger" @click="openDelProductModal(item)">
              刪除
            </button>
            <button class="btn btn-outline-success" @click="openEditModal(item)">
              修改
            </button>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
  <ProductModal ref="productModal" @refresh-products="getProducts"></ProductModal>
  <ProductDelModal
    ref="productDelModal"
    @refresh-products="getProducts"
    :itemName="tempProduct.title"
    :itemId="tempProduct.id"
  ></ProductDelModal>
  <ProductEditModal
    ref="productEditModal"
    @refresh-products="getProducts"
    :product="tempProduct"
  ></ProductEditModal>
</template>

<script>
import ProductModal from "../components/ProductModal.vue";
import ProductDelModal from "../components/ProductDelModal.vue";
import ProductEditModal from "@/components/ProductEditModal.vue";
import ToastList from "@/components/ToastList.vue";
export default {
  data() {
    return {
      products: {},
      isLoading: false,
      tempProduct: {},
    };
  },
  components: {
    ProductModal,
    ProductDelModal,
    ProductEditModal,
    ToastList,
  },
  methods: {
    getProducts() {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/admin/products`;
      this.$http.get(api).then((res) => {
        this.products = res.data.products;
        this.isLoading = false;
      });
    },
    openModal() {
      this.tempProducts = {};
      this.$refs.productModal.modalShow();
    },
    openEditModal(item) {
      this.tempProduct = { ...item };
      this.$refs.productEditModal.modalShow();
    },
    openDelProductModal(item) {
      this.tempProduct = { ...item };
      this.$refs.productDelModal.modalShow();
    },
  },
  created() {
    this.getProducts();
  },
};
</script>
